@import url('https://fonts.googleapis.com/css?family=Anton|Faster+One|Monoton|Saira+Stencil+One|Shrikhand|Syncopate&display=swap');

.App {
  text-align: center;
  font-size:14px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color:black
}

iframe {
  overflow:hidden;
}